import { filter } from "rxjs/operators";
import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { GeneralService } from "app/shared/services/General/general.service";
import { FileService } from "app/shared/services/General/upload-file.service";
import { AuthService } from "app/shared/services/auth/auth.service";
import { NgxSpinnerService } from "ngx-spinner";
import { LanguageService } from "app/shared/services/language.service";
import { SubjectByYearModel } from "app/student/models/student";
import { AppAssets } from "app/shared/data/app-assets";

@Component({
  selector: "app-educational-year-teacher-index",
  templateUrl: "./custome-educational-year-subjects.component.html",
  styleUrls: ["./custome-educational-year-subjects.component.scss"],
})
export class CustomEducationalYearSubjectsComponent implements OnInit {
  stageId: number;
  Subjects: SubjectByYearModel[] = [];
  yearId: number;

  constructor(
    private route: ActivatedRoute,
    private spinner: NgxSpinnerService,
    private fileService: FileService,
    public authService: AuthService,
    private GeneralService: GeneralService,
    public languageService: LanguageService,
    public appAssets: AppAssets
  ) {
    this.route.paramMap.subscribe(
      (params) => (this.yearId = Number(params.get("customYearId")))
    );
    this.route.paramMap.subscribe(
      (params) => (this.stageId = Number(params.get("stageId")))
    );
  }

  ngOnInit(): void {
    this.languageService.changeLanguage(
      localStorage.getItem("currentLang") ?? "ar"
    );

    this.spinner.show();
    this.GeneralService.getAllSubjectByCustomEducationalYear(
      this.yearId
    ).subscribe((res: any) => {
      if (res) {
        this.Subjects = res.subjectEducationalYears.filter(
          (subject) => subject.teacher_user_id > 0
        );
        console.log(this.Subjects);
        this.spinner.hide();
      }
    });
  }

  imageUrl(url: string) {
    return url
      ? this.fileService.previewPicture(url)
      : "assets/img/lectureBackground.jpeg";
  }

  navigationToSubject(
    teacher_user_id: number,
    teacherSubjectId: number,
    subjectId: number
  ) {
    // let url = `/student/subject-lectures/${this.yearId}/${subjectId}`;
    let url = `/student/teacher/${teacher_user_id}/teacher-subject/${teacherSubjectId}/${subjectId}`;

    if (this.authService.isUser()) {
      if (this.authService.isStudent()) return url;
      else return null;
    } else return "/auth/login";
  }
}
