import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { AppAssets } from "app/shared/data/app-assets";
import { IYearDropVM } from "app/shared/models/general/general";
import { GeneralService } from "app/shared/services/General/general.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-home-educational-years",
  templateUrl: "./home-educational-years.component.html",
  styleUrls: ["./home-educational-years.component.scss"],
})
export class HomeEducationalYearsComponent implements OnInit {
  years: IYearDropVM[] = [];
  stageId: number;
  constructor(
    private generalServ: GeneralService,
    private route: ActivatedRoute,
    private spinner: NgxSpinnerService,
    public appAssets: AppAssets
  ) {
    this.route.paramMap.subscribe(
      (param) => (this.stageId = Number(param.get("stageId")))
    );
  }

  ngOnInit(): void {
    this.getEduYearByStageId();
  }

  getEduYearByStageId() {
    this.spinner.show();
    this.generalServ
      .getEduYearByStageId<IYearDropVM[]>(this.stageId)
      .subscribe((res) => {
        this.years = res;
        this.spinner.hide();
      });
  }
}
