import { NgxSpinnerModule } from "ngx-spinner";
import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { IStudentReservationVM } from "app/admin/models/admin/reservation";
import { ReservationService } from "app/admin/services/Admin/reservation.service";
import { TeacherSubjectsService } from "app/student/services/teacherSubjects.service";
import { ToastrService } from "ngx-toastr";
import { NgxSpinnerService } from "ngx-spinner";
import {
  ITeacherSubjectsListVM,
  ITeacherSubjectsVM,
} from "app/student/models/student";
import { GeneralService } from "app/shared/services/General/general.service";
import { FileService } from "app/shared/services/General/upload-file.service";
import { AuthService } from "app/shared/services/auth/auth.service";

@Component({
  selector: "app-teacher-subjects-index",
  templateUrl: "./teacher-subjects-index.component.html",
  styleUrls: ["./teacher-subjects-index.component.scss"],
})
export class TeacherSubjectsIndexComponent implements OnInit {
  teacherSubjects: ITeacherSubjectsListVM;
  teacherUserId: number;
  Reservations?: IStudentReservationVM;
  constructor(
    private TeacherSubServ: TeacherSubjectsService,
    private router: Router,
    private route: ActivatedRoute,
    private toaster: ToastrService,
    private authService: AuthService,
    private ReservationService: ReservationService,
    private spinner: NgxSpinnerService,
    private GeneralService: GeneralService,
    public fileService: FileService
  ) {
    this.route.paramMap.subscribe(
      (params) => (this.teacherUserId = Number(params.get("teacherId")))
    );
  }

  ngOnInit(): void {
    // this.getReservationInfo();
    this.getSubjectByTeacherId();
  }

  getSubjectByTeacherId() {
    this.spinner.show();
    this.GeneralService.getSubjectByTeacherId(this.teacherUserId).subscribe(
      (res: ITeacherSubjectsListVM) => {
        if (res) {
          this.teacherSubjects = res;
          console.log(this.teacherSubjects);
          this.spinner.hide();
        }
      }
    );
  }

  imageUrl(path: string, teacherId: number) {
    let imagePath = null;
    if (path != null) {
      imagePath = path;
      return this.fileService.previewPicture(imagePath);
    } else if (teacherId == 22) {
      imagePath = "assets/img/licpic.jpeg";
    } else {
      imagePath = "assets/img/lectureBackground.jpeg";
    }
    console.log("path",path);
    console.log("img",imagePath);
    console.log("id",teacherId);

    return imagePath;
  }

  getReservationInfo() {
    this.ReservationService.getReservationInfo<IStudentReservationVM>(
      2
    ).subscribe((res) => {
      this.Reservations = res;
    });
  }

  navigationToSubject(teacherUserId, teacherSubjectId, subjectId) {
    let url = `/student/teacher/${teacherUserId}/teacher-subject/${teacherSubjectId}/${subjectId}`
    if (this.authService.isUser()) {
      if (this.authService.isStudent()) return url;
      else return null;
    } else return '/auth/login';

  }
}
